@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap" rel="stylesheet);
* {
  font-family: 'Open Sans', sans-serif;
}

.dayhours {
  width: 100%;
  height: 200px;
  border-radius: 25px 0 0 25px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 40px 25px;
  background-color: #76affd;
  overflow-x: scroll;
}

.dayhours  > .hourly {
  margin-left: 15px;
}

.dayhours  > .hourly:first-child {
  margin-left: 30px;
}

.header {
  margin: 2vh 40px;
}

.header h1 {
  margin: 0;
  color: #fff;
  font-size: 32px;
}

.header p {
  margin: 0;
  color: #fff;
  font-size: 14px;
}

.todayWeather {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 10vh 0;
}

.todayWeather__prognos {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.todayWeather__prognos img {
  margin: 0;
  width: 100px;
  height: 100px;
}

.todayWeather__prognos p {
  margin: 0;
  font-size: 36px;
  font-weight: 900;
}

.todayWeather h2 {
  font-size: 24px;
  color: #fff;
  margin: 0;
  padding-left: 10px;
}

.today_moredays {
  margin: 5vh 40px 25px;
}

.today_moredays a {
  text-decoration: none;
  font-size: 18px;
}

.today_moredays a:checked {
  background-color: transparent;
}

.today_moredays a:first-child {
  color: #fff;
  margin-right: 20px;
}

.today_moredays a:last-child {
  color: #bcbcbc;
  margin-right: 20px;
}
img {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    padding: 0;
}

p {
    padding: 0;
    margin: 0 auto;
    color: #fafae9;
    font-size: 16px;
}

.hourly {
    width: 60px;
    height: 120px;
    border-radius: 100vh;
    background-color: #3f76f2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    padding: 0;
}
/* Main menu positionning */
.main-nav {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    text-align: center;
    background: #FAFAE9;
    opacity: 0;
    visibility: hidden;
    transition: all .375s;
}

.main-nav.is-open {
    opacity: 1;
    z-index: 100;
    visibility: visible;
}

/* Yellow band effect */
.main-nav::before {
	content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: -15px;
    background: #506ED1;
    transform-origin: 0 0;
    transform: skew(-14deg) translateX(-120%);
    transition: all .275s .1s;
}

.main-nav.is-open::before {
    transform: skew(-14deg) translateX(0);
}

/* Skewing effect on menu links */
.main-nav ul {
    display: inline-flex;
    flex-direction: column;
    height: 93%; /* Should be 100%, but we have a notice message :D */
    align-items: flex-end;
    justify-content: center;
    transform: translateX(-18%) skew(-16deg);
}

.main-nav li {
    display: block;
    margin: .5rem 0;
    text-align: right;
    transform: skew(16deg);
}

/* Apparition effect on links */
.main-nav a {
    opacity: 0;
    transform: translateY(-10px);
}

.main-nav.is-open a {
    opacity: 1;
    transform: translateY(0);
}
.main-nav li:nth-child(1) a {
	transition: all 275ms 175ms
}
.main-nav li:nth-child(2) a {
	transition: all 275ms 225ms
}
.main-nav li:nth-child(3) a {
	transition: all 275ms 275ms
}
.main-nav li:nth-child(4) a {
	transition: all 275ms 325ms
}
.main-nav li:nth-child(5) a {
	transition: all 275ms 375ms
}


/* Decoration */
.main-nav ul,
.main-nav li {
	list-style: none;
	padding: 0;
}
.main-nav a {
	display: block;
	padding: 12px 0;
	color: #FAFAE9;
	font-size: 1.4em;
	text-decoration: none;
	font-weight: bold;
	outline: none;
	-moz-outline-style: none;
}

/* Burger Style: @see: https://codepen.io/CreativeJuiz/full/oMZNXy */
.open-main-nav {
	position: absolute;
	top: 15px;
	padding-top: 20px;
	right: 15px;
	z-index: 1000;
	background: none;
	border: 0;
	cursor: pointer;
}
.open-main-nav:focus {
	outline: none;
}
.burger {
	position: relative;
	display: block;
	width: 28px;
	height: 4px;
	margin: 0 auto;
	background: #fff;
	transform: skew(5deg);
	transition: all .275s;
}

.burger:after,
.burger:before {
	content: '';
	display: block;
	height: 100%;
	background: #fff;
	transition: all .275s;
}

.burger:after {
	transform: translateY(-12px) translateX(-2px) skew(-20deg);
}

.burger:before {
	transform: translateY(-16px) skew(-10deg);
}

/* Toggle State part */
.is-open .burger {
	transform: skew(5deg) translateY(-8px) rotate(-45deg);
}

.is-open .burger:before {
	transform: translateY(0px) skew(-10deg) rotate(75deg);
}

.is-open .burger:after {
	transform: translateY(-12px) translateX(10px) skew(-20deg);
    opacity: 0;
}

/* MENU Text part */

.burger-text {
	display: block;
	font-size: .675rem;
	letter-spacing: .05em;
	margin-top: .5em;
	text-transform: uppercase;
	font-weight: 500;
	text-align: center;
	color: #fff;
	text-decoration: none;
	outline: none;
	-moz-outline-style: none;
}

.container {
	position: absolute;
	top: 0; right: 0;
	bottom: 0; left: 0;
	overflow: hidden;
	visibility:hidden;
}


/* When slowmotion is activated */
.is-slowmo + .device .open-main-nav .burger,
.is-slowmo + .device .open-main-nav .burger:before,
.is-slowmo + .device .open-main-nav .burger:after,
.is-slowmo + .device .main-nav,
.is-slowmo + .device .main-nav::before,
.is-slowmo + .device .main-nav a {
	transition-duration: 3s;
}
.is-slowmo + .device .main-nav li:nth-child(1) a {
	transition-delay: 1750ms
}
.is-slowmo + .device .main-nav li:nth-child(2) a {
	transition-delay: 2250ms
}
.is-slowmo + .device .main-nav li:nth-child(3) a {
	transition-delay: 2750ms
}

/* Notice */
.notice {
	position: absolute;
	bottom: -15px;
	left: 0; right: 0;
	padding: 20px;
	background: #F2F2F2;
	color: #5A3B5D;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.5;
	z-index: 100;
	text-align: center;
}
.notice strong {
	font-weight: 700;
}
.notice a {
	padding: 2px 3px;
	background: #FEDC2A;
	text-decoration: none;
}
